<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus consultas
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Consultas pendientes</v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <div class="mb-2" style="color: #084068">{{ consultation.service.name }}</div>

          <div class="call-to-action">
            <v-row>
              <v-col class="text-center">
                <div>Costo del documento : S/. {{ consultation.price }}</div>
                <div>Código de solicitud: {{ consultation.id | padleft }}</div>
              </v-col>
            </v-row>
          </div>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <v-combobox v-model="form.lawyer_specialty_id" label="Especialidad" :items="specialties" item-text="name"
                      item-value="id"></v-combobox>
        </v-col>
        <v-col cols="12">
          <v-combobox v-model="form.ubigeo_department_id" label="Departamento" :items="departments" item-text="name"
                      item-value="id"></v-combobox>
        </v-col>
        <v-col cols="12">
          <v-btn @click="onSubmit" :loading="isLoading" color="primary" block>Continuar</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"

export default {
  name: 'RequestStart',
  inject: ['notyf'],
  data: () => ({
    isLoading: false,
    consultation: {},
    specialties: [],
    departments: [],
    form: {
      lawyer_specialty_id: '',
      ubigeo_department_id: '',
    }
  }),
  computed: {
    ...mapGetters(['user']),
  },
  mounted() {
    this.loadConsultation()
    this.loadSpecialties()
    this.loadDepartments()
  },
  methods: {
    loadConsultation() {
      let consultationId = this.$route.params.id
      let url = 'consultations/' + consultationId
      axios.get(url).then((response) => {
        this.consultation = response.data
      })
    },
    loadSpecialties() {
      let url = 'specialties'
      axios.get(url).then((response) => {
        this.specialties = response.data
      }).catch().then(() => {

      })
    },
    loadDepartments() {
      let url = 'ubigeo/departments'
      axios.get(url).then((response) => {
        this.departments = response.data
      }).catch().then(() => {

      })
    },
    onSubmit() {
      let consultationId = this.$route.params.id
      let url = 'consultations/' + consultationId
      let data = {
        lawyer_specialty_id: this.form.lawyer_specialty_id.id,
        ubigeo_department_id: this.form.ubigeo_department_id.id,
      }

      axios.put(url, data).then(() => {
        this.notyf.success('Informacion actualizada.');
        this.$router.push({name: 'RequestDetail', params: {id: consultationId}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      })
    }
  }
}
</script>
